import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpModule, JsonpModule } from '@angular/http';

//native  plugins
import { YoutubeVideoPlayer } from '@ionic-native/youtube-video-player';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Network } from '@ionic-native/network';
import { EmailComposer } from '@ionic-native/email-composer';
import { IonicStorageModule } from '@ionic/storage';
import { AdMobFree } from '@ionic-native/admob-free';


import { MyApp } from './app.component';


//providers
import { YoutubeProvider } from '../providers/youtube/youtube';
import { IgProvider } from '../providers/ig/ig';
import { DataProvider } from '../providers/data/data';
import { GithubProvider } from '../providers/github/github';

//plugins
import { IonicImageViewerModule } from 'ionic-img-viewer';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    JsonpModule,
    IonicModule.forRoot(MyApp, {
      tabsHideOnSubPages: true,
      modalEnter: 'modal-slide-in',
      modalLeave: 'modal-slide-out',
      pageTransition: 'ios-transition',
      tabsHighlight:true
    }, 
{
  links: [
    { loadChildren: '../pages/contact/contact.module#ContactPageModule', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/github/github.module#GithubPageModule', name: 'GithubPage', segment: 'github', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/githubmodal/githubmodal.module#GithubPageModule', name: 'GithubmodalPage', segment: 'githubmodal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/igmodal/igmodal.module#IgmodalPageModule', name: 'IgmodalPage', segment: 'igmodal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/instagram/instagram.module#InstagramPageModule', name: 'InstagramPage', segment: 'instagram', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/intro/intro.module#IntroPageModule', name: 'IntroPage', segment: 'intro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/pagetabs/pagetabs.module#PagetabsPageModule', name: 'PagetabsPage', segment: 'pagetabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/seminar/seminar.module#SeminarPageModule', name: 'SeminarPage', segment: 'seminar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/splash/splash.module#SplashPageModule', name: 'SplashPage', segment: 'splash', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/work/work.module#WorkPageModule', name: 'WorkPage', segment: 'work', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/youtube/youtube.module#YoutubePageModule', name: 'YoutubePage', segment: 'youtube', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/youtubemodal/youtubemodal.module#YoutubemodalPageModule', name: 'YoutubemodalPage', segment: 'youtubemodal', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicImageViewerModule,
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    YoutubeProvider,
    YoutubeVideoPlayer,
    IgProvider,
    InAppBrowser,
    DataProvider,
    Network,
    GithubProvider,
    EmailComposer,
    AdMobFree
  ]
})
export class AppModule { }



